<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <div></div>
            <div>
                <el-input
                        placeholder="请输入手机号/申请人"
                        size="small"
                        style="width: 220px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="请选择状态"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>


                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="工作室名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="负责人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="省">
            </el-table-column>
            <el-table-column
                prop="address"
                label="市">
            </el-table-column>
            <el-table-column
                prop="address"
                label="成员人数">
            </el-table-column>
            <el-table-column
                prop="address"
                label="护士认证数">
            </el-table-column>
            <el-table-column
                prop="address"
                label="抢单数">
            </el-table-column>
            <el-table-column
                prop="orderNumber"
                label="完成订单数">
            </el-table-column>
            <el-table-column
                prop="order"
                label="发出订单线索">
            </el-table-column>
            <el-table-column
                prop="status"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="crateTime"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="210">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isOpen" @click="enable(scope.row)" type="success" size="small"
                    >启用</el-button>
                    <el-button v-else @click="forbidden(scope.row)" type="danger" size="small"
                               >禁用</el-button>
                    <el-button @click="preview(scope.row)" type="info" size="small"
                               >预览</el-button>
                    <el-button @click="editor(scope.row)" type="warning" size="small"
                               >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    isOpen: true,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    isOpen: false,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            preview(row) {
                this.$router.push({ path: '/studio_preview_3'})
            },
            editor(row) {
                this.$router.push({ path: '/studio_editor_3'})
            },
            forbidden(row) {
                this.$confirm('确定禁用该工作室?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用该工作室成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用该工作室'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该工作室?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用该工作室成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用该工作室'
                    });
                });
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
